var Logger = class {
  constructor(options = {}) {
    this.logLevels = {
      "verbose": 0,
      "debug": 1,
      "info": 2,
      "warn": 3,
      "error": 4,
      "none": 5
      // Disable logging
    };
    this.level = options.level || "info";
    this.customLogger = options.customLogger;
  }
  setLevel(level) {
    this.level = level;
  }
  setCustomLogger(logger) {
    this.customLogger = logger;
  }
  verbose(...args) {
    this.log("verbose", ...args);
  }
  debug(...args) {
    this.log("debug", ...args);
  }
  info(...args) {
    this.log("info", ...args);
  }
  warn(...args) {
    this.log("warn", ...args);
  }
  error(...args) {
    this.log("error", ...args);
  }
  log(level, ...args) {
    if (this.logLevels[level] < this.logLevels[this.level]) {
      return;
    }
    if (this.customLogger) {
      this.customLogger(level, ...args);
    } else {
      switch (level) {
        default:
        case "verbose":
          console.log(...args);
          break;
        case "debug":
          console.debug(...args);
          break;
        case "info":
          console.info(...args);
          break;
        case "warn":
          console.warn(...args);
          break;
        case "error":
          console.error(...args);
          break;
      }
    }
  }
};
